.cli .grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.cli .grid>*:first-child {
    grid-column: 1 / span 2;
    grid-row: 1 / span 2;
}

@media (max-width: 768px) {

  
    .cli .grid{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }

    .cli .grid>*:first-child{
        grid-column: 1;
        grid-row: 1;
    }
}